import {
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  HostListener,
  Input,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import {ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR, NgForm, NgModel} from "@angular/forms";

@Component({
  selector: 'app-autocomplete-box',
  templateUrl: './autocomplete-box.component.html',
  styleUrls: ['./autocomplete-box.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => AutocompleteBoxComponent),
      multi: true
    }
  ]
})
export class AutocompleteBoxComponent implements ControlValueAccessor {
  registerOnChange(fn: any): void {
  }

  registerOnTouched(fn: any): void {
  }

  setDisabledState(isDisabled: boolean): void {
  }

  writeValue(obj: any): void {
  }

  @ViewChild('fieldId', {static: true}) public fieldId: NgModel;
  @Input() clsForm;
  @Input() inputText;
  @Output() inputTextChange = new EventEmitter();
  pattern;
  filteredItems = new Set();

  items = [
    {
      id: 123
    },
    {
      id: 1234
    },
    {
      id: 456
    },
    {
      id: 789
    },
    {
      id: 'E-008 EMERCHEMIE NB CEYLON LTD.'
    },
    {
      id: 'E-009 EMERCHEMIE NC CEYLON LTD.'
    },
    {
      id: 'E-009 emerchemiie NC CEYLON LTD.'
    }
  ];

  constructor() {

  }

  ngOnInit() {

  }

  suggestTexts() {
    this.pattern = new RegExp('(' + this.inputText + '|' + this.inputText.toLowerCase() + '|' + this.inputText.toUpperCase() + ')');
    // console.log(this.pattern.exec('E-009 emerchemiie NC CEYLON LTD.'))
    this.filteredItems = new Set();
    if (this.inputText !== '') {
      for (let item of this.items) {
        if (this.pattern.test(item.id.toString())) {
          this.filteredItems.add({
            id: item.id,
            startIndex: this.pattern.exec(item.id.toString()).index,
            endIndex: this.pattern.exec(item.id.toString()).index + this.inputText.length
          });
        }
      }
    }
    this.inputTextChange.emit(this.inputText)
  }

  selectText(item) {
    this.filteredItems.clear();
    this.inputText = item.id;
    this.inputTextChange.emit(this.inputText)
  }

  focusOut() {
    this.filteredItems.clear();
  }
}
