import {Component, OnInit, ViewChild} from '@angular/core';
import {AlertBoxService} from "../../alert-box/alert-box.service";
import {NgForm, NgModel} from "@angular/forms";

@Component({
  selector: 'app-supplier',
  templateUrl: './supplier.component.html',
  styleUrls: ['./supplier.component.css']
})
export class SupplierComponent implements OnInit {

  @ViewChild('supplierForm', {static: true}) public supplierForm: NgForm;
  @ViewChild('name', {static: true}) public name: NgModel;
  alertBox = {
    alert: false,
    msg: '',
    value: ''
  };
  tempSupplier;
  suppliers = [
    {
      name: 'Abc',
      email: 'abc@gmail.com',
      address: 'abc Road, Colombo',
      cNumber: '077-1234567',
      fNumber: '071-1234567'
    },
    {
      name: 'Abc',
      email: 'abc@gmail.com',
      address: 'abc Road, Colombo',
      cNumber: '077-1234567',
      fNumber: '071-1234567'
    }
  ];

  constructor(private alertService: AlertBoxService) {
    this.tempSupplier = this.getNewSupplier();
  }

  ngOnInit() {
  }

  addRow() {
    let supplier = this.getNewSupplier();
    supplier.name = this.tempSupplier.name;
    supplier.email = this.tempSupplier.email;
    supplier.address = this.tempSupplier.address;
    supplier.cNumber = this.tempSupplier.cNumber;
    supplier.fNumber = this.tempSupplier.fNumber;

    this.suppliers.unshift(supplier)
    this.supplierForm.resetForm();
    this.name.valueAccessor['fieldId'].control.markAsUntouched()
  }

  updateRow(index, supplier) {

  }

  removeRow(index, supplier) {
    this.alertBox.alert = true;
    this.alertBox.msg = 'Do you want to remove';
    this.alertBox.value = supplier.name + ' ?';
    this.alertService.reply.observers = [];
    this.alertService.reply.subscribe(reply => {
      if (reply) {
        this.suppliers.splice(index, 1);
      }
      this.alertBox.alert = false;
    })
  }

  setName(supplier, name) {
    supplier.name = name;
  }

  getNewSupplier() {
    return {
      name: '',
      email: '',
      address: '',
      cNumber: '',
      fNumber: ''
    }
  }
}
